.ui.form {
  font-size: 1.5rem;
}

.ui.form input[type=text] {
  border-radius: 25px;
}

.ui.form input[type=text]:focus {
  border-radius: 25px;
  -webkit-box-shadow: 0px 0px 5px 2px rgba(235,235,235,1);
  box-shadow: 0px 0px 5px 2px rgba(235,235,235,1);
  border-color: rgba(34,36,38,.15);
}
.field{
  margin-left: 40%;
  margin-top: 10%;
}


